.App {
  text-align: center;
  background-color: #fbdb1c;
  max-width: 1400px;
  align-content: center;
  display: inline-block;

}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-logo {
  height: 50px;;
}

.Carousel {
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  align-content: center;
  display: inline-block;

}

.Carousel-text {
  background: black;
  color: yellow;
  max-width: 300px
}

.Contact-Us {
  padding: 20px;
  background: #282c34;
  align-content: center;
}

.Contact-Us-text {
  color: white;
  padding-top: 10px;
}

.Contact-Us-form {
  max-width: 800px;
  display: inline-block;

}


.Main {
  align-items: center;
  justify-content: center;
  text-align: center;
  background: black;

}